header {
    width: 100%;
    background: #222;

    .inner {
        width: 1180px;
        height: 80px;
        margin: 0 auto;
        position: relative;

        h1 {
            position: absolute;
            bottom: 20px;
            left: 0;

            a {
                font: bold 24px/1 'arial';
                color: #fff;
            }
        }
        #gnb {
            position: absolute;
            bottom: 0;
            right: 0;

            li {
                float: left;

                a {
                    display: inline-block;
                    padding: 22px 30px;
                    font: bold 14px/1 'arial';
                    color: #fff;
                }
            }
        }

        .menuMo {
            position:absolute;
            right : 30px;
            bottom: 20px;
            font-size :24px;
            color : #fff;
            display : none;
        }
    }

    &.main {
        background: transparent;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
    }
}


//tablet
@media screen and (max-width: $tablet){
    header {    
        .inner {
            width: 100%;
            h1 {
               
                left: 30px;
    
            }
            #gnb {
               display:none;
        }
            .menuMo {
                display:block;
            }
    }
} 
}

